<template>
  <div>
    <Header name="注册账号"></Header>
    <div class="block">
      <div v-if="showForm">
        <van-form @submit="onSubmit">
          <van-field name="radio" label="请选择账号类型" class="field">
            <template #input>
              <van-dropdown-menu>
                <van-dropdown-item v-model="form.userType" :options="options" @change="handleChange"/>
              </van-dropdown-menu>
            </template>
          </van-field>
          <img
            :src="require('@/assets/image/shouji.png')"
            alt=""
            class="phone"/>
          <van-field
            v-model="form.phone"
            name="用户名"
            label=""
            placeholder="请输入手机号码"
            :rules="[{ required: true }]"/>
          <img
            :src="require('@/assets/image/yzm.png')"
            alt=""
            class="password"
            style="width: 19px; height: 21px; margin-top: 18px; left: 18px"/>
          <van-field
            v-model="form.verificationCode"
            center
            clearable
            label=""
            placeholder="请输入验证码"
            :rules="[{ required: true }]"
          >
          <template #button>
            <span @click="getNumber()" v-if="countDown" style="width: 19px; height: 21px; margin-top: 22px; left: 18px; color: #999999;"><span style="color: #D6D6D6; margin-right: 15px;">|</span>获取验证码</span>
            <span  v-else style="width: 19px; height: 21px; margin-top: 22px; left: 18px; color: #999999;"><span style="color: #D6D6D6; margin-right: 15px;">|</span>{{time}}</span>
          </template>
          </van-field>
          <img
            :src="require('@/assets/image/mima.png')"
            alt=""
            class="password"/>
          <van-field
            v-model="form.password"
            type="password"
            name="密码"
            label=""
            placeholder="请输入8-16位数字或字母组成的密码"
            :rules="[{ required: true }, {validator, message: '密码不能小于六位数'}]"/>
          <img
            :src="require('@/assets/image/mima.png')"
            alt=""
            class="password"/>
          <van-field
            v-model="form.passwords"
            type="password"
            name="密码"
            label=""
            placeholder="请再次输入密码"
            :rules="[{ required: true }, {validator, message: '密码不能小于六位数'}]"/>
          <div style="margin: 46px 6.6% 0px">
            <van-button
              color="#ff4c4c"
              block
              native-type="submit"
              v-if="form.backgroundAudit === 0">
              注册
            </van-button>
            <van-button
              color="#ff4c4c"
              block
              native-type="submit"
              v-if="form.backgroundAudit === 1">
              下一步
            </van-button>
          </div>
        </van-form>
      </div>
      <div class="nextMsg" v-else>
        <van-form @submit="onSubmitObj">
          <img
            :src="require('@/assets/image/lxr.png')"
            alt=""
            class="password"
            style="width: 16px; height: 16px; margin-top: 20px; left: 18px"/>
          <van-field
            v-model="form.linkman"
            name="联系人"
            label=""
            placeholder="联系人"
            :rules="[{ required: true }]"/>
          <img
            :src="require('@/assets/image/shouji.png')"
            alt=""
            class="phone"/>
          <van-field
            v-model="form.linkPhone"
            name="联系电话"
            label=""
            placeholder="联系电话"
            :rules="[{ required: true }]"/>
          <img
            :src="require('@/assets/image/lxr.png')"
            alt=""
            class="password"
            style="width: 16px; height: 16px; margin-top: 20px; left: 18px"/>
          <van-field
            v-model="form.company"
            name="企业名称"
            label=""
            placeholder="企业名称"
            :rules="[{ required: true }]"/>
          <img
            :src="require('@/assets/image/gsdz.png')"
            alt=""
            class="password"
            style="width: 18px; height: 20px; margin-top: 18px; left: 18px"/>
          <van-field
            v-model="form.address"
            name="公司地址"
            label=""
            placeholder="公司地址"
            :rules="[{ required: true }]"/>
          <!-- <div class="imgUpload">
            <p>上传公司营业执照或其他证件</p>
            <van-uploader v-model="fileList" multiple :max-count="5" />
          </div> -->
          <div style="margin: 20px 6.6% 0px" class="next">
            <van-button
              color="#ff4c4c"
              style="width: 100%;"
              native-type="submit"
              @click="flag = 'js'">
              完成
            </van-button>
          </div>
        </van-form>
      </div>
      <div class="tologin">
        已有账号&nbsp;&nbsp;<span @click="tologin">立即登录</span>
      </div>
      <div class="rules">
        继续即代表你同意<span @click="touserAgm">《用户协议》</span>和<span @click="toprivacy">《隐私政策》</span>
      </div>
    </div>
  </div>
</template>

<script>

import * as api from '@/data/api/market/index'
import Header from '@/components/Layout/header/index'
// import axios from 'axios';

export default {
  components :{
    Header
  },
  data() {
    return {
      countDown:true,
      time:60,
      baseUrl: process.env.BaseUrl + '/minio/upload',
      show: false,
      flag: 'nl',
      form: {
        backgroundAudit: '',
        userType: '1',
        phone: '',
        verificationCode: '',
        password: '',
        passwords: '',
        linkman: '',
        linkPhone: '',
        company: '',
        address: '',
        imageUrl: []
      },
      //请输入8-16位数字或字母组成的密码
      validator(val) {
        var len = val.length;
        if( len >= 6) {
          return true
        }else {
          return false
        }
      },
      showForm: true,
      fileList: [],
      options: []
    };
  },
  mounted(){
    this.getMemberList()
  },
  methods: {
    toprivacy(){
      this.$router.push({
            path:'/user/privacyP'
          })
    },
    touserAgm(){
      this.$router.push({
        path:'/user/userAgm'
      })
    },
    handleChange(value){
      let self = this
      self.options.map(item=>{
        if (item.id === Number(value)) {
          self.form.backgroundAudit = item.backgroundAudit
        }
      })
    },
    getMemberList () {
      let self = this
      api.getMemberTypeList({status: 1, backgroundAdd: 1}).then(res => {
        self.form.userType = res.data.list[0].id + ''
        self.options = res.data.list.map(item=>{
          return{
            'text':item.name,
            'value': item.id + '',
            backgroundAdd: item.backgroundAdd,
            backgroundAudit: item.backgroundAudit,
            id: item.id,
            name: item.name,
            note: item.note,
            sort: item.sort,
            status: item.status,
          }
        })
        self.form.backgroundAudit = self.options[0].backgroundAudit
      }).catch((res) => {
        self.$message.error(res.message);
      })
    },
    onSubmit () {
      let me = this
      if (me.form.backgroundAudit === 0) {
        if (me.form.password === me.form.passwords) {
          api.register(me.form).then(res => {
            if (res.code === 200) {
              me.$notify({
                type: 'success',
                message: '注册成功，正在前往登录！'
              });
              me.$nextTick(()=> {
                me.$router.push({
                  path:'/user/login/index',
                })
              }, 1000)
            } else {
              me.$notify({
                message: res.message,
                color: '#ad0000',
                background: '#ffe1e1',
              })
            }
          }).catch((res) => {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          })
        } else {
          me.$notify({
            message: "两次密码不一致",
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }
      } else {
        me.showForm = false
      }
    },
    onSubmitObj () {
      let me = this
      // if (me.fileList && me.fileList.length > 0) {
        me.afterReadImg(me.fileList)
      // } else {
      //   me.$notify({
      //     message: "请上传公司营业执照或其他证件",
      //     color: '#ad0000',
      //     background: '#ffe1e1',
      //   })
      // }
    },
    tologin () {
      this.$router.push({
        path:'/user/login/index'
      })
    },
    // 验证码
    getNumber () {
      let me = this
      if (me.form.phone) {
        api.getVerificationCode({'phone' : me.form.phone}).then(res => {
          if (res.code === 200) {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            });
            me.countDown = false
            let timer = setInterval(()=>{
              me.time--
              if(me.time === 0){
                me.countDown = true
                me.time = 60
                clearInterval(timer)
              }
            },1000)
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        })
      } else {
        me.$notify({
          message: "手机号码不能为空",
          color: '#ad0000',
          background: '#ffe1e1',
        })
      }
    },
    afterReadImg (lists) {
      let me = this
      // me.form.imageUrl = []
      // let num = lists.length
      // lists && lists.map((file, index)=> {
      //   var formData = new FormData();
      //   formData.append('file', file.file);
      //   const instance=axios.create({ withCredentials: true})
      //   let config = { headers: { "Content-Type": "multipart/form-data" }};
      //   instance.post(me.baseUrl, formData, config).then(res => {
      //     if (res.data.code === 200 ) {
            // me.form.imageUrl.push(res.data.data.url)
            // if (num === index + 1) {
              // 完成
              api.register(me.form).then(res => {
                if (res.code === 200) {
                  me.$notify({
                    type: 'success',
                    message: '注册资料提交成功，请等待后台审核！'
                  });
                  me.$nextTick(()=> {
                    me.$router.push({
                      path:'/user/login/index',
                    })
                  }, 1000)
                } else {
                  me.$notify({
                    message: res.message,
                    color: '#ad0000',
                    background: '#ffe1e1',
                  })
                }
              }).catch((res) => {
                me.$notify({
                  message: res.message,
                  color: '#ad0000',
                  background: '#ffe1e1',
                })
              })
            // }
      //     } else {
      //       me.$notify({
      //         message: res.data.message,
      //         color: '#ad0000',
      //         background: '#ffe1e1',
      //       })
      //     }
      //   }).catch((res) => {
      //     me.$notify({
      //       message: res.message,
      //       color: '#ad0000',
      //       background: '#ffe1e1',
      //     })
      //   }).finally(() => {
      //   })
      // })
    }
  },
};
</script>

<style lang="stylus" scoped>
>>>.van-dropdown-menu__bar{
  box-shadow: none;
}
.field{
  display: flex;
  align-items: center;
}
.van-button__text {
  font-size: 16px;
}
.logo {
  width: 40%;
  height: 40px;
  position: relative;
  left: 30%;
  top: 50px;
  text-align: center;

  img {
    width: 150px;
    height: 40px;
  }
}

.loginbody {
  width: 100%;
  height: 178px;
  position: relative;
  left: 0px;
  top: 130px;

  >>>.van-cell {
    padding: 15px 4.26%;
    border-bottom: 1px solid #ECECEC;
    width: 90%;
    margin-left: 5%;
  }

  >>>.van-cell::after {
    left: 4.26%;
  }

  >>>.van-field__control {
    padding-left: 15px;
  }

  .phone {
    width: 13px;
    height: 20px;
    position: absolute;
    z-index: 1;
    left: 4.8%;
    top: 18px;
  }

  .password {
    width: 17px;
    height: 19px;
    position: absolute;
    z-index: 1;
    left: 4.8%;
    top: 70px;
  }
}

.loginbtm {
  position: relative;
  left: 6.6%;
  top: 185px;
  width: 86.6%;
  height: 13px;
  display: flex;
  justify-content: space-between;
  font-size: 13px;
  color: #fd2744;
}

.rules {
  position: absolute;
  left: 16.53%;
  bottom: 35px;
  width: 67.2%;
  font-size: 12px;
  color: #888888;
  text-align: center;

  span {
    color: #fd2744;
  }
}

.block {
  position: relative;
  width: 100%;
  height: 715px;
  background: #fff;

  >>>.van-field__label {
    width: 36%;
  }
  >>>.van-cell {
    padding: 16px 16px;
    border-bottom: 1px solid #ECECEC;
    width: 90%;
    margin-left: 5%;
  }

  >>>.van-field__control {
    padding-left: 15px;
  }

  .imgUpload {
    width: 90.7%;
    margin: 27px 0px 27px 24px;
    >>>.van-uploader__preview{
      margin 0px 4px 8px 0px
    }
    p {
      margin-bottom 21px
      font-size: 14px;
      color: #555555;
    }
  }
  .tologin {
    position: absolute;
    left: 16.53%;
    width: 67.2%;
    font-size: 13px;
    color: #888888;
    text-align: center;
    margin-top: 43px;

    span {
      color: #FD2744;
    }
  }

  .password {
    width: 17px;
    height: 19px;
    position: absolute;
    z-index: 1;
    left: 20px;
    margin-top: 17px;
  }

  .phone {
    width: 13px;
    height: 20px;
    position: absolute;
    z-index: 1;
    left: 20px;
    margin-top: 18px;
  }
  .next{
    display flex
    justify-content space-between
    >>>.van-button{
      width 43%
    }
  }
}
</style>


